import React, {Component} from 'react';
// import Welcome from '../Welcome/Welcome';
import Input from '../../component/Input/Input';
import classes from './Form.css';
import axios from '../../axiosForm';
import cover from '../../images/newyorker cover.jpg';
import title from '../../images/title.png';

class Form extends Component{
state={
	formDetail:{
		name:{
			elementType: 'input',
			label:'name',
			elementConfig:{
				type: 'text'
			},
			validation:{
				required: true
			},
			value:'',
			valid: false,
			touched: false
		},
		email:{
			elementType: 'email',
			label:'email',
			elementConfig:{
				type: 'text'			
			},
			validation:{
				required: true,
				isEmail: true
			},
			value:'',
			valid: false,
			touched: false
		},
		phoneNo: {
			elementType: 'input',
			label:'Phone no.',
			elementConfig:{
				type: 'text'
			},
			validation:{
				required: true,
				isNumeric: true,
				minLength: 10,
				maxLength: 10
			},
			value:'',
			touched: false,
			valid: false
		},
		City:{
			elementType: 'input',
			label:'city',
			elementConfig:{
				type: 'text',
				maxLength: '10'
			},
			value:'',
			valid: true
		}
	},
	formIsValid: false, signIn: false, loading: false
};

submitHandler= (event)=>{
	this.setState({
		loading:true
	});
	event.preventDefault();
	 let data = {
      name: this.state.formDetail.name.value,
      email: this.state.formDetail.email.value,
      message: "Thank you for subscribing to our newsletter, we'll send you daily updates. "
	  }
	  console.log(data);
	  setTimeout(()=>{
	  	this.setState({
	  		loading:false 
	  	});
	  	this.props.history.push('/welcome')
	  },2000)
	// axios.post('/formDetail.json', data).then(res =>{
	// 	console.log(res.data);
	// 	this.restForm();
	// }).catch(err =>{
	// 	console.log(err);
	// });	
}
restForm(){
	this.setState({
		loading:false,
		formDetail:{
			name:{value:''},
			email:{value:''},
			phoneNo:{value:''},
			City:{value:''}
		} 
	});
}
checkValidity(value,rules){
	let isValid= true;
	if(!rules)
		{return true;}
	if(rules.required)
		isValid= value.trim() !== '' && isValid;
	if(rules.minLength)
		isValid= value.length >= rules.minLength && isValid;
	if(rules.maxLength)
		isValid= value.length <=rules.maxLength && isValid;
	if(rules.isNumeric){
		const pattern= /^\d+$/;
		isValid= pattern.test(value) && isValid;
	}
	if(rules.isEmail){
		const mail= /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
		isValid= mail.test(value) && isValid;
	}
	return isValid;
}
changeHandler= (event, id)=>{
	event.preventDefault();
	const updatedForm= {...this.state.formDetail};
	const updatedFormElement= {...updatedForm[id]};
	updatedFormElement.value= event.target.value;
	updatedForm[id]= updatedFormElement;
	updatedFormElement.valid= this.checkValidity(updatedFormElement.value, updatedFormElement.validation);
	updatedFormElement.touched= true;
	let formIsValid= true;
	for(let el in updatedForm){
		formIsValid= updatedForm[el].valid && formIsValid;
	}
	console.log(formIsValid);
	this.setState({formDetail: updatedForm,
	    formIsValid: formIsValid
	});
}
render() {
	console.log();
	const formElm=[];
	for(let key in this.state.formDetail){
		formElm.push({
			id: key,
			attribute: this.state.formDetail[key]
		});
	}
	return (
		<div className= {`container ${classes.Form}`}>
			{this.state.loading ? <div className={classes.loading}>
			<div className={classes.innerload}>
				<div style={{left:"38px",top:"38px",animationDelay:"0s"}}></div>
				<div style={{left:"80px",top:"38px",animationDelay:'0.125s'}}></div>
				<div style={{left:"122px",top:"38px",animationDelay:'0.25s'}}></div>
				<div style={{left:"38px",top:"80px",animationDelay:'0.875s'}}></div>
				<div style={{left:"122px",top:"80px",animationDelay:'0.375s'}}></div>
				<div style={{left:"38px",top:"122px",animationDelay:'0.75s'}}></div>
				<div style={{left:"80px",top:"122px",animationDelay:'0.625s'}}></div>
				<div style={{left:"122px",top:"122px",animationDelay:'0.5s'}}></div>
				</div>
			</div> :null}
			<div className={`col-md-8 ${classes.margin}`}>
				<img src="https://media.giphy.com/media/26tnffyD8Rt8rIQ4E/giphy.gif" alt="cover"
					className={classes.gif}/>
				<div className={classes.info}>
					<i className={`fas fa-quote-left ${classes.quoteLeft}`}></i>
					<p className={classes.para}>Never miss a big New Yorker story again.
					 Sign up for This Weekâ€™s Issue and get an e-mail every week with the stories
					  you have to read.</p>
					<i className={`fas fa-quote-right ${classes.quoteRight}`}></i>
				</div>
			</div>
			<div className={`col-md-4 ${classes.wrapper}`}>
				<img src={title} alt="title" className={classes.title}/>
				<form onSubmit={this.submitHandler} >
					{
						formElm.map(el =>(
							<Input key= {el.id}
								elementType= {el.attribute.elementType}
								elementConfig= {el.attribute.elementConfig}
								value= {el.value}
								label= {el.attribute.label}
								invalid= {!el.attribute.valid}
								touched= {el.attribute.touched}
								shouldvalidate= {el.attribute.validation}
								changed= {(event)=> this.changeHandler(event, el.id)}/>
						))
					}
					<button className= {classes.Button}
						disabled= {!this.state.formIsValid}>Submit</button>
				</form>
			</div>
		</div>
	);
}
}
export default Form;